<template>
    <div id="new-member">
        <h3>New member</h3>
        <div class="row">
            <form @submit.prevent="saveMember" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model.number="member_id" required>
                        <label>Member ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="house" required>
                        <label>House</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model.number="points" required>
                        <label>Points</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-member',
    data () {
        return {
            member_id: 0,
            name: null,
            house: null,
            points: 0
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('members').orderBy('member_id', 'desc').get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.member_id = doc.data().member_id
                    vm.name = doc.data().name
                    vm.house = doc.data().house
                    vm.points = doc.data().points
                })
            })
        })
    },
    methods: {
        saveMember () {
            db.collection('members').add({
                member_id: this.member_id,
                name: this.name,
                house: this.house,
                points: this.points
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>


